import React from 'react';
import _ from 'lodash';
import {graphql} from 'gatsby';

import components, {Layout} from '../components/index';
import Banner from '../components/Banner';

export default class Home extends React.Component {
    render() {
        return (
            <Layout {...this.props}>
                <Banner {...this.props} />
                {_.map(_.get(this.props, 'data.page.frontmatter.sections'), (section, section_idx) => {
                    let GetSectionComponent = components[_.get(section, 'component')];
                    return (
                        <GetSectionComponent key={section_idx} {...this.props} section={section} site={this.props.data.site} />
                    )
                })}
            </Layout>
        );
    }
}


export const query = graphql`
    query($url: String!) {
        page: markdownRemark(fields: { url: { eq: $url } }) {
            frontmatter {
                sections {
                    component
                    section_id
                    background_color_style
                    title
                    style
                    subtitle
                }
                title
                template
                banner {
                    section_id
                    title
                    subtitle
                    actions {
                        url
                        is_primary
                        is_scrolly
                        label
                    }
                }
            }
        }
        site {
            siteMetadata {
                title
                menu {
                    actions {
                        url
                        is_primary
                        title
                    }
                }
                footer {
                    social_links {
                        icon
                        link_url
                        text
                    }
                    copyright
                }
            }
            pathPrefix
        }
        menus {
            main {
                identifier
                title
                weight
                url
                items {
                    identifier
                    title
                    weight
                    url
                    items {
                        identifier
                        title
                        weight
                        url
                    }
                }
            }
        }
        pages_urls: allMarkdownRemark {
            edges {
                node {
                    fields {
                        relativePath
                        url
                    }
                }
            }
        }
        pages_carousel: allMarkdownRemark {
            edges {
                node {
                    frontmatter {
                        home_sections {
                            carousel {
                                enabled
                                weight
                                data_position
                                image
                                title
                                text
                                button_background_color
                            }
                        }
                    }
                    fields {
                        url
                    }
                }
            }
        }
        pages_posts: allMarkdownRemark(filter: { fields: { relativeDir: { eq: "posts" } } }) {
            edges {
                node {
                    frontmatter {
                        date
                        home_image
                        title
                        excerpt
                        home_button_color
                    }
                    fields {
                        url
                    }
                }
            }
        }
        pages_spotlight: allMarkdownRemark {
            edges {
                node {
                    frontmatter {
                        home_sections {
                            spotlight {
                                enabled
                                weight
                                section_id
                                background_accent
                                data_position
                                image
                                title
                                subtitle
                                text
                                button_accent
                            }
                        }
                    }
                    fields {
                        url
                    }
                }
            }
        }
    }
`;
